body {
  letter-spacing: -.025em;
  word-break: keep-all;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

body.oxy-nav-menu-prevent-overflow {
  overflow-y: visible;
}

h1, h2, h3, h4, h5, h6, p, span, a, input, button, ul, li {
  font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  z-index: 10;
  background-color: #fff;
  position: sticky;
  top: 0;
}

.header#header.oxy-pro-menu-open .oxy-pro-menu-mobile-open-icon {
  display: flex;
}

.header#header .oxy-pro-menu-mobile-open-icon {
  justify-content: flex-start;
  align-items: center;
  gap: initial;
  flex-direction: row;
  width: calc(100% - 30px);
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
}

.header.oxy-pro-menu.oxy-pro-menu-open > .oxy-pro-menu-container {
  width: 50%;
}

@media screen and (width <= 600px) {
  .header.oxy-pro-menu.oxy-pro-menu-open > .oxy-pro-menu-container {
    width: 100%;
  }
}

.header .oxy-pro-menu-list {
  gap: 36px;
  max-height: 90vh;
}

.header li.menu-item > a:after {
  padding-left: 1em;
  font-size: 16px;
  font-weight: 400;
}

.footer {
  color: #eee;
  background-color: #222;
  font-size: 12px;
  line-height: 2;
}

.footer > .ct-section-inner-wrap {
  padding: 90px 0;
}

.footer__cols {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
  display: flex;
}

.footer__info {
  justify-content: flex-start;
  align-items: center;
  gap: initial;
  flex-direction: row;
  display: flex;
}

.main {
  color: #fff;
  background-color: #141414;
  min-height: 100vh;
}

.main > .ct-section-inner-wrap {
  width: 100%;
  padding: 0;
}

.section > .ct-section-inner-wrap {
  justify-content: flex-start;
  align-items: center;
  gap: initial;
  flex-direction: column;
  width: calc(100% - 30px);
  max-width: 1110px;
  margin: 0 auto;
  padding: 160px 0;
  display: flex;
}

.section__cols-10 > .ct-section-inner-wrap {
  width: calc(100% - 30px);
  max-width: 920px;
}

.video-background {
  pointer-events: none;
  display: none;
}

.headline-box {
  width: calc(100% - 30px);
  max-width: 540px;
  margin-right: auto;
}

.headline-box__index {
  color: #d91a83;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

.headline-box__head {
  padding: .5em 0;
  font-size: 48px;
  font-weight: bold;
  line-height: 1.3;
}

.headline-box__body {
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

.package-slider__card {
  background-color: #fff;
  width: 540px;
}

.package-slider__card--inactive {
  display: none;
}

.package-slider__card a {
  color: #111;
}

.package-slider__card h3, .package-slider__card h3 > span {
  text-transform: capitalize;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
  font-size: 28px;
  font-weight: 700;
}

.package-slider__controllers {
  justify-content: space-between;
  align-items: center;
  gap: initial;
  flex-direction: row;
  width: calc(100% - 30px);
  max-width: 920px;
  margin: 0 auto;
  padding-bottom: 80px;
  display: flex;
}

.filter-model {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 14px;
  display: flex;
}

.filter-model__item {
  opacity: .7;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.filter-model__item--active {
  opacity: 1;
}

.price-tag__item {
  justify-content: flex-start;
  align-items: center;
  gap: initial;
  flex-direction: row;
  display: flex;
}
/*# sourceMappingURL=custom.css.map */
